ter;\n  justify-content: center;\n}\n\n.pomodoro-container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  text-align: center;\n  padding: 2rem;\n  min-height: 80vh;\n  width: 100%;\n}\n\n.logo-container svg {\n  width: 335px;\n  height: auto;\n  animation: logoFloat 3s ease-in-out infinite;\n}\n\n@keyframes logoFloat {\n  0%, 100% { transform: translateY(0px); }\n  50% { transform: translateY(-10px); }\n}\n\n.logo-placeholder {\n  font-size: 4rem;\n  color: var(--primary-color);\n  margin-bottom: 2rem;\n  animation: logoFloat 3s ease-in-out infinite;\n}\n\n.main-title {\n  font-family: var(--font-family-primary);\n  font-size: var(--h1-font-size);\n  color: var(--secondary-color);\n  margin-bottom: 1rem;\n  text-transform: uppercase;\n  letter-spacing: 2px;\n  animation: fadeInUp 1s ease-out;\n}\n\n@keyframes fadeInUp {\n  from {\n    opacity: 0;\n    transform: translateY(30px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n.phase-title {\n  font-family: var(--font-family-primary);\n  font-size: var(--h2-font-size);\n  color: var(--primary-color);\n  text-transform: uppercase;\n  letter-spacing: 1px;\n  animation: bounceIn 0.8s ease-out;\n}\n\n@keyframes bounceIn {\n  0% {\n    opacity: 0;\n    transform: scale(0.3);\n  }\n  50% {\n    opacity: 1;\n    transform: scale(1.05);\n  }\n  70% {\n    transform: scale(0.9);\n  }\n  100% {\n    opacity: 1;\n    transform: scale(1);\n  }\n}\n\n.phase-transition-overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background-color: rgba(248, 153, 57, 0.95);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  z-index: 1000;\n  opacity: 0;\n  transition: opacity 0.5s ease;\n}\n\n.phase-transition-overlay.active {\n  opacity: 1;\n}\n\n.transition-content {\n  text-align: center;\n  color: white;\n}\n\n.logo-animation {\n  margin-bottom: 2rem;\n  animation: logoSpin 2s ease-in-out infinite;\n}\n\n.logo-animation svg {\n  width: 150px;\n  height: auto;\n  filter: brightness(0) invert(1);\n}\n\n@keyframes logoSpin {\n  0%, 100% { \n    transform: scale(1) rotate(0deg); \n  }\n  25% { \n    transform: scale(1.1) rotate(5deg); \n  }\n  50% { \n    transform: scale(1.2) rotate(0deg); \n  }\n  75% { \n    transform: scale(1.1) rotate(-5deg); \n  }\n}\n\n.transition-message {\n  font-family: var(--font-family-primary);\n  font-size: 4rem;\n  text-transform: uppercase;\n  letter-spacing: 2px;\n  animation: slideUp 0.8s ease-out;\n}\n\n@keyframes slideUp {\n  from {\n    opacity: 0;\n    transform: translateY(50px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n.session-counter {\n  font-size: 1.2rem;\n  font-family: var(--font-family-secondary);\n  color: var(--secondary-color);\n}\n\n.timer-display {\n  font-family: var(--font-family-primary);\n  font-size: var(--timer-font-size);\n  color: var(--secondary-color);\n  font-weight: normal;\n}\n\n.goal-display {\n  font-family: var(--font-family-primary);\n  font-size: 4rem;\n  color: var(--primary-color);\n  padding: 1rem;\n  border: 2px solid var(--primary-color);\n  border-radius: 8px;\n  background-color: rgba(248, 153, 57, 0.1);\n  text-transform: uppercase;\n  letter-spacing: 1px;\n}\n\n.start-button {\n  font-family: var(--font-family-primary);\n  font-size: var(--button-font-size);\n  background-color: var(--primary-color);\n  color: var(--text-light);\n  border: none;\n  padding: 1.5rem 3rem;\n  border-radius: 8px;\n  cursor: pointer;\n  transition: all 0.3s ease;\n  text-transform: uppercase;\n  letter-spacing: 1px;\n  margin-bottom: 1em;\n}\n\n.start-button:hover {\n  background-color: var(--secondary-color);\n  transform: translateY(-2px);\n}\n\n.goal-label {\n  font-family: var(--font-family-primary);\n  font-size: 1.5rem;\n  color: var(--secondary-color);\n  margin-bottom: 1rem;\n  text-transform: uppercase;\n  letter-spacing: 1px;\n}\n\n.goal-input {\n  font-size: 1.2rem;\n  padding: 1rem;\n  border: 2px solid var(--primary-color);\n  border-radius: 8px;\n  width: 100%;\n  max-width: 500px;\n  text-align: center;\n  background-color: var(--background-color);\n  color: var(--secondary-color);\n}\n\n.goal-input:focus {\n  outline: none;\n  border-color: var(--secondary-color);\n}\n\n.timer-controls {\n  display: flex;\n  gap: 1rem;\n  flex-wrap: wrap;\n  justify-content: center;\n}\n\n.control-button {\n  font-family: var(--font-family-secondary);\n  font-size: 1rem;\n  padding: 0.75rem 1.5rem;\n  border: 2px solid var(--secondary-color);\n  border-radius: 6px;\n  background-color: var(--background-color);\n  color: var(--secondary-color);\n  cursor: pointer;\n  transition: all 0.3s ease;\n}\n\n.control-button:hover {\n  background-color: var(--secondary-color);\n  color: var(--text-light);\n}\n\n.pause-button {\n  border-color: var(--primary-color);\n  color: var(--primary-color);\n}\n\n.pause-button:hover {\n  background-color: var(--primary-color);\n  color: var(--text-light);\n}\n\n.goal-help {\n  font-size: 0.9rem;\n  color: var(--secondary-color);\n  opacity: 0.7;\n  margin-bottom: 1rem;\n  font-style: italic;\n}\n\n.video-container {\n  margin: 2rem 0;\n  width: 100%;\n  width: 1080px;\n  height: 720px;\n}\n\n.youtube-player {\n  width: 100%;\n  border-radius: 8px;\n}\n\n.video-placeholder {\n  background-color: rgba(248, 153, 57, 0.1);\n  border: 2px dashed var(--primary-color);\n  padding: 3rem;\n  border-radius: 8px;\n  margin: 2rem 0;\n  color: var(--secondary-color);\n  font-style: italic;\n}\n\n/* Focus styles for accessibility */\nbutton:focus,\ninput:focus {\n  outline: 3px solid var(--primary-color);\n  outline-offset: 2px;\n}\n\n/* Reduced motion support */\n@media (prefers-reduced-motion: reduce) {\n  * {\n    animation-duration: 0.01ms !important;\n    animation-iteration-count: 1 !important;\n    transition-duration: 0.01ms !important;\n  }\n}\n\n@media (max-width: 768px) {\n  .pomodoro-container { padding: 1rem; }\n  .main-title { font-size: 3rem; }\n  .phase-title { font-size: 2.5rem; }\n  .timer-display { font-size: 4rem; }\n  .goal-display { font-size: 1.5rem; }\n  .start-button { font-size: 1.2rem; padding: 1rem 2rem; }\n  .timer-controls { flex-direction: column; align-items: center; }\n  .control-button { width: 200px; }\n  .youtube-player { height: 200px; }\n  .transition-message { font-size: 2.5rem; }\n}\n"],"sourceRoot":""}]);
